<template>
  <div class="account">
    <van-nav-bar title="账号管理" left-text="返回" left-arrow @click-left="back" />

    <van-cell-group>
      <van-cell class="v-cell" title="头像">
        <div class="clearfix">
          <div class="fr user-img">
            <img class="auto-img" :src="allUserInfo.userImg" alt />
            <van-uploader class="upload-box" :after-read="uploadFile" />
          </div>
        </div>
      </van-cell>
      <van-cell class="v-cell" title="用户id" :value="allUserInfo.userId"></van-cell>
      <van-cell class="v-cell" title="手机号" :value="allUserInfo.phone"></van-cell>
      <van-cell class="v-cell" title="昵称">
        <div class="clearfix">
          <div class="fr">
            <van-field @blur="updateUserInfo({key: 'nickName', value: allUserInfo.nickName, url: 'http://www.kangliuyong.com:10002/updateNickName'})" class="v-filed" v-model="allUserInfo.nickName" input-align="right" />
          </div>
        </div>
      </van-cell>
      <van-cell>
        <div class="clearfix">
          <div class="fl v-cell-text">简介</div>
          <div class="fl v-cell-textarea">
            <textarea @change="updateUserInfo({key: 'desc', value: allUserInfo.desc, url: 'http://www.kangliuyong.com:10002/updateDesc'})" class="textarea" v-model="allUserInfo.desc"></textarea>
          </div>
        </div>
      </van-cell>
    </van-cell-group>
  </div>
</template>

<script>
// import "../assets/css/userManage.less";
export default {
  data() {
    return {
      allUserInfo: {}
    };
  },

  created() {
    this.getUserAllInfo();
  },

  methods: {
    //获取用户信息
    getUserAllInfo() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "http://www.kangliuyong.com:10002/findAccountInfo",
        params: {
          appkey: 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=',
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == "B001") {
            this.allUserInfo = result.data.result[0];
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    uploadFile(file) {
      

    
      let base64Img=file.content.split('64,')[1];

      
      let imgType = file.file.type.split('/')[1];

      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "http://www.kangliuyong.com:10002/updateAvatar",
        data: {
          appkey: 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=',
          tokenString,
          imgType,
          serverBase64Img: base64Img
        }
      })
        .then(result => {
          this.$toast.clear();
          

          if (result.data.code == 'H001') {
            this.allUserInfo.userImg = result.data.userImg;
          } else {
            this.$toast(result.data.msg);
          }
         
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    
    updateUserInfo(modifyData) {
      let tokenString = localStorage.getItem("__Tk");


      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: modifyData.url,
        data: {
          appkey: 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=',
          tokenString,
          [modifyData.key]: modifyData.value
        }
      })
        .then(result => {
          this.$toast.clear();
          
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    back() {
      this.$router.go(-1);
    }
  }
};
</script>
<style lang="less" scoped>
.account {
    .user-img{
      width: 40px;
      height: 40px;
      background-color: #ddd;
      position: relative;
    }
    .upload-box{
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      overflow: hidden;
      opacity: 0;
    }
    .v-cell{
      line-height: 40px;
    }
    .v-cell-text{
      width: 40px;
    }
    .v-cell-textarea{
      width: calc(~"100% - 40px");
      height: 60px;
    }
    .textarea{
      display: block;
      width: 100%;
      height: 60px;
      padding: 0;
      margin: 0;
      border: none;
      resize: none;
      text-align: right;
    }
  }
</style>